@import url(https://fonts.googleapis.com/css?family=Roboto);
*{box-sizing:border-box;margin:0;padding:0}:root{--primary-color: black;--dark-color: #323232;--lighter-color: #eeeef3;--white-color: white;--danger-color: #f73859;--accent-color: #f4e04d;--box: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);--darkish-color: #1e2022}body{font-family:'Roboto', sans-serif;font-size:1rem;background-color:#eeeef3;background-color:var(--lighter-color);color:black}body #root .container{min-height:100vh}.container{max-width:1100px;margin:auto;overflow:hidden;padding:0 2rem}.text-green{color:#4cbbb9}.text-red{color:#f73859;color:var(--danger-color)}.light-mode{transition:1.5s}.loading-para{background:#eeeef3 !important;background:var(--lighter-color) !important;color:#eeeef3 !important;color:var(--lighter-color) !important;margin-top:10px}.loading-header{background:#eeeef3 !important;background:var(--lighter-color) !important;color:#eeeef3 !important;color:var(--lighter-color) !important;width:10rem;height:5rem}.dark-mode{background:black;background:var(--primary-color);transition:1.5s}.dark-mode #navbar{background:#323232;background:var(--dark-color)}.dark-mode #navbar .title-wrapper{background-color:black;background-color:var(--primary-color);color:#eeeef3;color:var(--lighter-color)}.dark-mode #navbar nav ul li a{background-color:black;background-color:var(--primary-color)}.dark-mode #navbar nav ul li a:hover{color:black;color:var(--primary-color);background:#eeeef3;background:var(--lighter-color)}.dark-mode #today-date{background:#323232;background:var(--dark-color);color:#eeeef3;color:var(--lighter-color);box-shadow:none}.dark-mode #today-date h2{color:#eeeef3;color:var(--lighter-color)}.dark-mode #total-cases #country-name{color:#eeeef3;color:var(--lighter-color)}.dark-mode #total-cases #card-container .card{background:#323232;background:var(--dark-color);box-shadow:none}.dark-mode #total-cases #card-container .card h2{color:white;color:var(--white-color)}.dark-mode #total-cases #card-container .card p{color:#eeeef3;color:var(--lighter-color)}.dark-mode #total-cases #card-container .card .text-green{color:#639a67}.dark-mode #total-cases #card-container .card .text-red{color:#f73859;color:var(--danger-color)}.dark-mode #btn-container a{box-shadow:none}.dark-mode #btn-container a:first-child{background:#323232;background:var(--dark-color);color:#eeeef3;color:var(--lighter-color)}.dark-mode #timeline{box-shadow:none}.dark-mode #timeline h2{background:#323232;background:var(--dark-color);color:#eeeef3;color:var(--lighter-color);border-bottom:1px solid #eeeef3;border-bottom:1px solid var(--lighter-color)}.dark-mode #timeline div{background:#323232;background:var(--dark-color);color:#eeeef3;color:var(--lighter-color)}.dark-mode #graph{color:#eeeef3;color:var(--lighter-color);min-height:100vh}.dark-mode #graph #title div a{box-shadow:none}

